
import React, { Component } from 'react'
// import PropTypes from 'prop-types'
import { compose } from 'recompose';
import { isEmpty } from 'lodash';
import { withFirebase } from 'Helpers/Firebase';
import { connect } from 'react-redux';
import './QuanLyDonHang.scss';
import { Button } from 'reactstrap'
import { OrderDetailContent } from './components/OrderDetailContent/OrderDetailContent';
import { getCustomers, getDonHangs, updateDonHang } from 'reduxStore';
import { toast } from 'react-toastify';
import * as ROUTES from 'constants/Routes';

const dangLayDuLieu = 'Đang lấy dữ liệu';
const donHangKhongTonTai = 'Đơn hàng không tồn tại';

export class ChiTietDonHang extends Component {
  static propTypes = {

  }

  state = {
    donhang: null,
    isDonhang: true,
    isEdit: false,
  }

  updateDonHangHandle = ({ newDonHang }) => {
    const { donhang } = this.state;
    const { firebase: { auth: { currentUser: { email } } } } = this.props;
    const { sanPhams } = newDonHang;
    for (let i = 0; i < sanPhams.length; i++) {
      const sanpham = sanPhams[i];
      if (isEmpty(sanpham.tenSanPham) || isEmpty(sanpham.soLuong) || isEmpty(sanpham.donViTinh) ||
        isEmpty(sanpham.donGia)
      ) {
        toast.error(`Sản phẩm ${i + 1} thiếu dữ liệu.`)
        return null;
      }
    }

    let checkUpdate = window.confirm('Xác nhận lưu thay đổi');
    if (!checkUpdate) return null;
    newDonHang.modifiedAt = new Date().getTime();
    newDonHang.modifiedBy = email;
    newDonHang.isUpdated = 'Đã chỉnh sửa';
    newDonHang.sanPhamsName = '';
    newDonHang.sanPhams.forEach(sanPham => {
      newDonHang.sanPhamsName += `${sanPham.tenSanPham}, `;
    })
    this.props.updateDonHang({ newDonHang })
    this.setState({
      donhang: {
        ...newDonHang,
        sanPhams: newDonHang.sanPhams.map(donhang => { return { ...donhang } })
      }
    })
    toast.success('Update thành công!')
  }

  goBack = () => {
    const { history } = this.props;
    history.push(ROUTES.orderManage);
  }

  componentDidMount() {
    const { firebase } = this.props;
    const params = this.props.match.params;
    const donHangUrl = `/donhangs/2019/${params.id}`;
    firebase.db.ref(donHangUrl).once('value').then(snapshot => {
      const donhang = snapshot.val();
      const isDonhang = donhang ? true : false;
      this.setState({ donhang, isDonhang })
    });
    window.scrollBy(-10000, -10000);

    this.props.getCustomers();
  }

  render() {
    const { donhang, isDonhang } = this.state;
    const { match: { params: { id: maDonHang } } } = this.props;
    return (
      <div className="donhang">
        <div className="donhang__header">
          <Button onClick={this.goBack} color="warning" size="sm" className="mr-2"><i className="fa fa-long-arrow-left"></i> Quay lại</Button>
          <h5 className="donhang__header__title">Đơn hàng {maDonHang}</h5>
        </div>
        <div className="donhang__content">
          {
            donhang ?
              <OrderDetailContent
                donhang={this.state.donhang}
                customers={this.props.customers}
                updateDonHangHandle={this.updateDonHangHandle}
                {...this.props}
              />
              :
              <h4 className="mt-4 text-center">{isDonhang ? dangLayDuLieu : donHangKhongTonTai}</h4>
          }
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => {
  return {
    donhangs: state.sacMauMoi.donhangs,
    customers: state.sacMauMoi.customers,
  }
}

const enhanlers = compose(
  connect(mapStateToProps, { getDonHangs, getCustomers, updateDonHang }),
  withFirebase,
)

export default enhanlers(ChiTietDonHang)
