import React, { Component } from 'react'
import { forEach, isEmpty } from 'lodash'
import PropTypes from 'prop-types'
import { Row, Col, Label, FormGroup, Input, Button } from 'reactstrap';
import './OrderDetailContent.scss';
import reactDom from 'react-dom';
import SanPhamIn from './SanPhamIn';
import { formatMoney } from 'services/SacMauMoiService';
import { SanPham } from 'models';
import TrangThaiComponent from 'components/_shared/TrangThaiComponent/TrangThaiComponent';
import { DANG_LAM, DA_HUY, DA_GIAO } from '../../../../../constants/Constants';
import { UpdatedOrders } from '../UpdatedOrders/UpdatedOrders';

export class OrderDetailContent extends Component {
  static propTypes = {
    donhang: PropTypes.object.isRequired,
    customers: PropTypes.object.isRequired,
  }

  state = {
    newDonHang: null,
  }

  onChangeTamUng = (e) => {
    if (this.props.donhang.trangThai === DA_HUY || this.props.donhang.trangThai === DA_GIAO) { return };
    console.log('onChangeTamUng')
    const { newDonHang } = this.state;
    let tamUng = e.target.value
    if (tamUng > newDonHang.tongTien) {
      tamUng = newDonHang.tongTien;
    }
    console.log(tamUng);
    newDonHang.conLai = Number(newDonHang.tongTien) - Number(tamUng);
    this.setState({
      newDonHang: {
        ...newDonHang,
        tamUng
      }
    })
  }

  checkTamUng = (e) => {
    if (this.props.donhang.trangThai === DA_HUY || this.props.donhang.trangThai === DA_GIAO) { return };

    const { donhang } = this.props;
    const { newDonHang } = this.state;
    let tamUng = Number(e.target.value);
    if (tamUng < 0) {
      tamUng = donhang.tamUng
      newDonHang.conLai = Number(newDonHang.tongTien) - Number(tamUng);
      this.setState({
        newDonHang: {
          ...newDonHang,
          tamUng
        }
      })
    }
  }

  onChangeSanPhamIn = ({ name, value, index }) => {
    if (this.props.donhang.trangThai === DA_HUY || this.props.donhang.trangThai === DA_GIAO) { return };

    const { newDonHang } = this.state;
    const { sanPhams } = newDonHang;
    sanPhams[index][name] = value;
    sanPhams[index]['thanhTien'] = Number(sanPhams[index]['soLuong']) * Number(sanPhams[index]['donGia']);
    newDonHang.tongTien = 0;
    for (const sanpham of sanPhams) {
      newDonHang.tongTien += sanpham.thanhTien;
    }
    newDonHang.conLai = Number(newDonHang.tongTien) - Number(newDonHang.tamUng);

    this.setState({
      newDonHang: {
        ...newDonHang,
        sanPhams,
      }
    })

  }

  onChangeKhachHang = (e) => {
    if (this.props.donhang.trangThai === DA_HUY || this.props.donhang.trangThai === DA_GIAO) { return };
    const { newDonHang } = this.state;
    const value = e.target.value;
    const name = e.target.name;
    this.setState({
      newDonHang: {
        ...newDonHang,
        khachhang: {
          ...this.state.khachhang,
          [name]: value,
        }
      }

    })
  }

  onBlurKhachHang = () => {
    if (this.props.donhang.trangThai === DA_HUY || this.props.donhang.trangThai === DA_GIAO) { return };
    const { newDonHang: { khachhang }, newDonHang } = this.state;
    const { customers, donhang } = this.props;
    if (isEmpty(customers[khachhang.congTy])) {
      this.setState({
        newDonHang: {
          ...newDonHang,
          khachhang: donhang.khachhang
        }
      })
    } else {
      this.setState({
        newDonHang: {
          ...newDonHang,
          khachhang: customers[khachhang.congTy]
        }
      })
    }
  }

  renderCustomerDatalist = () => {
    const { customers } = this.props;
    const result = [];
    if (customers) {
      forEach(customers, (customer) => {
        result.push(<option key={customer.congTy} value={customer.congTy} />)
      })
    }
    return result;
  }

  deleteSanPhamIn = ({ index }) => {
    if (this.props.donhang.trangThai === DA_HUY || this.props.donhang.trangThai === DA_GIAO) { return };
    const { newDonHang } = this.state;
    newDonHang.sanPhams = newDonHang.sanPhams.filter((sanpham, i) => i !== index)
    this.setState({ newDonHang })
  }

  addNewSanPhamIn = () => {
    if (this.props.donhang.trangThai === DA_HUY || this.props.donhang.trangThai === DA_GIAO) { return };
    const { newDonHang } = this.state;
    newDonHang.sanPhams.push(new SanPham());
    this.setState({ newDonHang });
  }

  updateDonHangHandle = () => {
    if (this.props.donhang.trangThai === DA_HUY || this.props.donhang.trangThai === DA_GIAO) { return };
    const { newDonHang } = this.state;
    this.props.updateDonHangHandle({ newDonHang });
  }

  resetDonHang = () => {
    if (this.props.donhang.trangThai === DA_HUY || this.props.donhang.trangThai === DA_GIAO) { return };
    const { donhang } = this.props;
    let isComfirmed = window.confirm('Xác nhận hủy những thay đổi trong đơn hàng.')
    if (!isComfirmed) return null;

    const newDonHang = {
      ...this.props.donhang,
      sanPhams: donhang.sanPhams.map(sanpham => { return { ...sanpham } })
    }
    this.setState({ newDonHang })
  }

  chooseUpdatedOrder = ({ donhang }) => {
    const newDonHang = { ...donhang, sanPhams: donhang.sanPhams.map(donhang => { return { ...donhang } }) }
    this.setState({ newDonHang })
  }

  renderThongTin = () => {
    const { newDonHang } = this.state;
    const datetime = new Date(newDonHang.modifiedAt);
    const ngayTaoMessage = newDonHang.createAt === newDonHang.modifiedAt ? 'Tạo ngày' : 'Sửa ngày';
    return `${ngayTaoMessage}: ${datetime.getDate()}-${datetime.getMonth() + 1}-${datetime.getFullYear()} Lúc: ${datetime.getHours()}:${datetime.getMinutes()} bởi: ${newDonHang && newDonHang.modifiedBy ? newDonHang.modifiedBy : 'admin'}`
  }

  componentWillMount() {
    const { donhang } = this.props;
    const newDonHang = {
      ...this.props.donhang,
      sanPhams: donhang.sanPhams.map(sanpham => { return { ...sanpham } })
    }
    if (isEmpty(newDonHang.tamUng)) {
      newDonHang.tamUng = 0;
    }
    this.setState({ newDonHang })
  }

  componentDidMount() {
    reactDom.findDOMNode(this.refs.khachHangRef).setAttribute('autocomplete', "off")
  }

  render() {
    const { donhang } = this.props;
    const { newDonHang } = this.state;
    const { newDonHang: { tongTien, tamUng, conLai, khachhang, trangThai } } = this.state;
    const isDeleted = newDonHang.sanPhams.length > 1;
    const isDisabledButtons = DA_GIAO === donhang.trangThai || donhang.trangThai === DA_HUY;
    return (
      <div className="mt-2">
        <Row>
          <Col sm="12"><div className="py-2">{this.renderThongTin()}</div></Col>
        </Row>
        <Row>
          <Col sm="9">
            <div className="detail__left mb-3 p-2">
              <Row>
                <Col sm='4'>
                  <FormGroup>
                    <Label htmlFor="congTy">Khách Hàng</Label>
                    <Input type="text" name="congTy" id="congTy" tabIndex={1}
                      value={khachhang.congTy} list="customers"
                      onChange={this.onChangeKhachHang}
                      onBlur={this.onBlurKhachHang}
                      ref="khachHangRef"
                    />
                    <datalist id="customers">{this.renderCustomerDatalist()}</datalist>
                  </FormGroup>
                </Col>

                <Col sm="4">
                  <FormGroup>
                    <Label htmlFor="soDienThoai">Điện Thoại</Label>
                    <Input value={khachhang.soDienThoai} type="phone" name="soDienThoai" id="soDienThoai" placeholder="số điện thoại" disabled />
                  </FormGroup>
                </Col>
                <Col sm="4">
                  <FormGroup>
                    <Label htmlFor="trangThai">Trạng thái</Label>
                    <div>
                      <TrangThaiComponent trangThai={trangThai} />
                    </div>
                  </FormGroup>
                </Col>
              </Row>
              <Row>
                <Col sm='12'>
                  <h5 className="text-center sanphaminStyle">Sản phẩm in</h5>
                  {
                    newDonHang.sanPhams.map((sanpham, index) => {
                      return <SanPhamIn
                        key={index} sanpham={sanpham} index={index}
                        onChangeSanPhamIn={this.onChangeSanPhamIn}
                        deleteSanPhamIn={this.deleteSanPhamIn}
                        isDeleted={isDeleted}
                      />
                    })
                  }
                </Col>
              </Row>
              <hr />
              <Row >
                <Col sm='4'>
                  <Label>Tổng tiền</Label>
                  <FormGroup>
                    <h5 className="text-danger"><strong>{formatMoney(tongTien)}</strong></h5>
                  </FormGroup>
                </Col>
                <Col sm='4'>
                  <Label htmlFor="tamUng">Tạm ứng: <strong className="text-danger">{formatMoney(tamUng)}</strong></Label>
                  <FormGroup>
                    <Input
                      type="number"
                      name="tamUng"
                      id="tamUng"
                      value={tamUng ? tamUng : ''}
                      onChange={this.onChangeTamUng}
                      onBlur={this.checkTamUng}
                      min="0" step="1"
                    />
                  </FormGroup>
                </Col>
                <Col sm='4'>
                  <Label>Còn lại</Label>
                  <FormGroup className="conLai">
                    <h5 className="text-danger"><strong>{formatMoney(conLai)}</strong></h5>
                  </FormGroup>
                </Col>
              </Row>
              <FormGroup row>
                <Col xs='12'>
                  <Button outline color="warning" onClick={this.addNewSanPhamIn} className="mr-2" disabled={isDisabledButtons}><i className="fa fa-plus"></i> Thêm sản phẩm in</Button>
                  <Button outline color="danger" className="mr-2" onClick={this.resetDonHang} disabled={isDisabledButtons}>Hủy thay đổi</Button>
                  <Button color="success" onClick={this.updateDonHangHandle} disabled={isDisabledButtons}><i className="fa fa-save" /> Lưu thay đổi</Button>
                </Col>
              </FormGroup>
            </div>
          </Col>
          <Col sm="3">
            <div className="detail__right p-2">
              <UpdatedOrders
                donhang={this.props.donhang}
                firebase={this.props.firebase}
                chooseUpdatedOrder={this.chooseUpdatedOrder}
              />
            </div>
          </Col>
        </Row>
      </div>
    )
  }
}

export default OrderDetailContent
