import React, { Component } from 'react'
// import PropTypes from 'prop-types'
import { Label, Col, Input, FormGroup, Button } from 'reactstrap';
import { formatMoney } from 'services/SacMauMoiService';

export class SanPhamIn extends Component {
  static propTypes = {
  }

  deleteSanPhamIn = () => {
    const { index, sanpham } = this.props;
    const check = window.confirm(`Xác nhận xóa sản phẩm "${sanpham.tenSanPham}" ra khỏi đơn hàng?`)
    if (!check) return;
    this.props.deleteSanPhamIn({ index })
  }

  onChangeSanPham = (e) => {
    const { index, onChangeSanPhamIn } = this.props;
    const name = e.target.name;
    const value = e.target.value;
    onChangeSanPhamIn({ name, value, index });
  }

  render() {
    const { index } = this.props;
    const { sanpham: { moTa, donGia, thanhTien, donViTinh, soLuong, tenSanPham } } = this.props;
    return (
      <div>
        <FormGroup row>
          <Col xs="12"><Label><strong>Sản phẩm {index + 1}:</strong>
            <Button size="sm" color="danger" className="ml-2" disabled={!this.props.isDeleted}
              onClick={this.deleteSanPhamIn}
            ><i className="fa fa-trash"></i> Xóa</Button></Label></Col>

          <Col xs="4" style={{ paddingRight: 0 }}>
            <Label>Tên</Label>
             <Input className="form-control" name="tenSanPham"
              value={tenSanPham} onChange={this.onChangeSanPham}
            />
          </Col>

          <Col xs="2" style={{ paddingRight: 0 }}>
            <Label htmlFor="donViTinh">ĐVT</Label>
            <Input type="text" name="donViTinh" id="donViTinh"
              value={donViTinh} onChange={this.onChangeSanPham}
            />
          </Col>

          <Col xs="3">
            <Label htmlFor="soLuong">SL</Label>
            <Input type="number" name="soLuong" id="soluong"
              min='0' step='1 '
              value={(soLuong)} onChange={this.onChangeSanPham}
            />
          </Col>
          <Col xs="3">
            <Label htmlFor="donGia">Đơn giá: <strong>{formatMoney(donGia)}</strong></Label>
            <Input type="number" name="donGia" id="dongia"
              min='0' step='100'
              value={donGia} onChange={this.onChangeSanPham}
            />
          </Col>
        </FormGroup>

        <FormGroup row>
          <Col xs='8'>
            <Label htmlFor="moTa">Mô tả sản phầm</Label>
            <textarea className="form-control" rows='1'
              type="text" name="moTa" id="moTa"
              value={moTa} onChange={this.onChangeSanPham}
            />
          </Col>
          <Col xs='4'>
            <Label htmlFor="thanhtien">Thành tiền</Label>
            <FormGroup className="thanhtien">
              <h5><strong>{formatMoney(thanhTien)}</strong></h5>
            </FormGroup>
          </Col>
        </FormGroup>
      </div>
    )
  }
}

export default SanPhamIn
