import React, { Component } from 'react'
// import PropTypes from 'prop-types'
import { toArray } from 'lodash'
import { ListGroup, ListGroupItem } from 'reactstrap';


export class UpdatedOrders extends Component {
  static propTypes = {

  }

  state = {
    updatedDonHangs: {},
  }

  addDonHangElement = ({ donhang }) => {
    const { updatedDonHangs } = this.state;
    this.setState({
      updatedDonHangs: {
        ...updatedDonHangs,
        [donhang.modifiedAt]: { ...donhang }
      }
    })
  }

  chooseUpdatedOrder = (keyId) => {
    const { updatedDonHangs } = this.state;
    const donhang = updatedDonHangs[keyId]
    this.props.chooseUpdatedOrder({ donhang })
  }

  componentDidMount() {
    const { firebase, donhang } = this.props;
    const updatedDonHangsURL = `/updatedDonHangs/${donhang.maDonHang}`
    this.listen = firebase.db.ref(updatedDonHangsURL);
    this.listen.on('child_added', snapshot => {
      const donhang = snapshot.val();
      this.addDonHangElement({ donhang })
    })
  }

  componentWillUnmount() {
    this.listen.off();
  }

  getDate = (time) => {
    const { updatedDonHangs } = this.state;
    const donhang = updatedDonHangs[time]
    const datetime = new Date(time);
    const ngayTaoMessage = donhang.createAt === donhang.modifiedAt ? 'Tạo ngày' : 'Sửa ngày';
    return `${ngayTaoMessage}: ${datetime.getDate()}-${datetime.getMonth() + 1}-${datetime.getFullYear()} Lúc: ${datetime.getHours()}:${datetime.getMinutes()} bởi: ${donhang.modifiedBy}`
  }

  render() {
    const { updatedDonHangs } = this.state;
    const updatedDonHangsArray = toArray(updatedDonHangs).sort((x, y) => { return y.modifiedAt - x.modifiedAt });
    if (!updatedDonHangs) return <div className="text-center">Chưa chỉnh sửa</div>
    return (
      <ListGroup>
        {updatedDonHangsArray.map(donhang => {
          return <ListGroupItem action onClick={() => this.chooseUpdatedOrder(donhang.modifiedAt)} key={donhang.modifiedAt}>{this.getDate(donhang.modifiedAt)}</ListGroupItem>
        })}
      </ListGroup>
    )
  }
}

export default UpdatedOrders
